<template>
  <div class="admin-article-manager">
    <div class="manager-header">
      <h2>æ–‡ç« ç®¡ç†</h2>
      <div class="header-actions">
        <BaseButton
          variant="primary"
          @click="handleCreate"
        >
          + æ–°å»ºæ–‡ç« 
        </BaseButton>
        <BaseButton
          variant="secondary"
          @click="handleRefresh"
        >
          åˆ·æ–°
        </BaseButton>
      </div>
    </div>

    <div class="articles-section">
      <BaseCard class="content-card">
        <div
          v-if="loading"
          class="loading-container"
        >
          <LoadingSpinner />
        </div>
        
        <div
          v-else-if="articles.length === 0"
          class="empty-state"
        >
          <p>æš‚æ— æ–‡ç« æ•°æ®</p>
        </div>

        <div
          v-else
          class="articles-table-container"
        >
          <table class="data-table">
            <thead>
              <tr>
                <th>ID</th>
                <th>æ ‡é¢˜</th>
                <th>ä½œè€…</th>
                <th>åˆ†ç±»</th>
                <th>å‘å¸ƒæ—¶é—´</th>
                <th>çŠ¶æ€</th>
                <th>æ“ä½œ</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="article in articles"
                :key="article.id"
              >
                <td>{{ article.id }}</td>
                <td>{{ article.title }}</td>
                <td>{{ article.author?.name || 'æœªçŸ¥' }}</td>
                <td>{{ article.category }}</td>
                <td>{{ article.publishDate }}</td>
                <td>
                  <span :class="['status-badge', article.isPublished ? 'published' : 'draft']">
                    {{ article.isPublished ? 'å·²å‘å¸ƒ' : 'è‰ç¨¿' }}
                  </span>
                </td>
                <td class="actions-cell">
                  <button
                    class="action-btn edit"
                    @click="handleEdit(article)"
                  >
                    ç¼–è¾‘
                  </button>
                  <button
                    class="action-btn delete"
                    @click="handleDelete(article)"
                  >
                    åˆ é™¤
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        
        <div
          v-if="totalPages > 1"
          class="pagination-container"
        >
          <BasePagination 
            :current="currentPage" 
            :total="totalElements"
            :page-size="pageSize"
            @change="handlePageChange" 
          />
        </div>
      </BaseCard>
    </div>

    <BaseModal
      v-model:show="showEditModal"
      :title="editingArticle ? 'ç¼–è¾‘æ–‡ç« ' : 'æ–°å»ºæ–‡ç« '"
    >
      <form
        class="edit-form"
        @submit.prevent="saveArticle"
      >
        <div class="form-group">
          <label>æ ‡é¢˜</label>
          <input
            v-model="formData.title"
            type="text"
            required
            class="form-input"
          >
        </div>
        <div class="form-group">
          <label>æ‘˜è¦</label>
          <textarea
            v-model="formData.summary"
            class="form-textarea"
          />
        </div>
        <div class="form-group">
          <label>å†…å®¹</label>
          <textarea
            v-model="formData.content"
            class="form-textarea content-editor"
          />
        </div>
        <div class="form-group">
          <label>åˆ†ç±»</label>
          <input
            v-model="formData.category"
            type="text"
            class="form-input"
          >
        </div>
        <div class="form-group">
          <label>çŠ¶æ€</label>
          <div class="checkbox-group">
            <input
              id="isPublished"
              v-model="formData.isPublished"
              type="checkbox"
            >
            <label for="isPublished">å‘å¸ƒ</label>
          </div>
        </div>
        <div class="form-actions">
          <BaseButton
            type="button"
            variant="secondary"
            @click="showEditModal = false"
          >
            å–æ¶ˆ
          </BaseButton>
          <BaseButton
            type="submit"
            variant="primary"
            :loading="saving"
          >
            ä¿å­˜
          </BaseButton>
        </div>
      </form>
    </BaseModal>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted, reactive } from 'vue'
import { getArticles, createArticle, updateArticle, deleteArticle } from '@/api/article'
import type { Article } from '@/types/entities'
import BaseButton from '@/components/common/BaseButton.vue'
import BaseCard from '@/components/common/BaseCard.vue'
import BaseModal from '@/components/common/BaseModal.vue'
import BasePagination from '@/components/common/BasePagination.vue'
import LoadingSpinner from '@/components/common/LoadingSpinner.vue'
import { onAdminRefresh } from '@/utils/adminEvents'
import { recordAdminOperation } from '@/composables/useAdminLogs'

const articles = ref<Article[]>([])
const loading = ref(false)
const saving = ref(false)
const currentPage = ref(1)
const pageSize = ref(10)
const totalPages = ref(1)
const totalElements = ref(0)
const showEditModal = ref(false)
const editingArticle = ref<Article | null>(null)
const cleanupFns: Array<() => void> = []

type ArticleLogAction = 'create' | 'update' | 'delete' | 'refresh'

const logArticleAction = (action: ArticleLogAction, message: string, result: 'success' | 'failure' = 'success', targetId?: string | number) => {
  recordAdminOperation({
    entity: 'articles',
    action,
    message,
    targetId,
    result
  }).catch((error) => {
    console.warn('è®°å½•æ–‡ç« æ“ä½œæ—¥å¿—å¤±è´¥:', error)
  })
}

const formData = reactive<Partial<Article>>({
  title: '',
  summary: '',
  content: '',
  category: '',
  isPublished: false
})

const fetchArticles = async () => {
  loading.value = true
  try {
    const res = await getArticles({ page: currentPage.value - 1, size: pageSize.value })
    if (Array.isArray(res)) {
      articles.value = res
      totalPages.value = 1
      totalElements.value = res.length
    } else {
      articles.value = res.content || []
      totalPages.value = res.totalPages || 1
      totalElements.value = res.totalElements || 0
    }
  } catch (error) {
    console.error('Failed to fetch articles:', error)
  } finally {
    loading.value = false
  }
}

const handleRefresh = () => {
  fetchArticles()
  logArticleAction('refresh', 'æ‰‹åŠ¨åˆ·æ–°æ–‡ç« åˆ—è¡¨')
}

const handlePageChange = (page: number) => {
  currentPage.value = page
  fetchArticles()
}

const handleCreate = () => {
  editingArticle.value = null
  Object.assign(formData, {
    title: '',
    summary: '',
    content: '',
    category: '',
    isPublished: false
  })
  showEditModal.value = true
}

const handleEdit = (article: Article) => {
  editingArticle.value = article
  Object.assign(formData, article)
  showEditModal.value = true
}

const handleDelete = async (article: Article) => {
  if (!confirm(`ç¡®å®šè¦åˆ é™¤æ–‡ç«  "${article.title}" å—ï¼Ÿ`)) return
  try {
    await deleteArticle(article.id)
    fetchArticles()
    logArticleAction('delete', `åˆ é™¤æ–‡ç« ã€Œ${article.title}ã€`, 'success', article.id)
  } catch (error) {
    alert('åˆ é™¤å¤±è´¥')
    logArticleAction('delete', `åˆ é™¤æ–‡ç« ã€Œ${article.title}ã€å¤±è´¥`, 'failure', article.id)
  }
}

const saveArticle = async () => {
  saving.value = true
  try {
    if (editingArticle.value) {
      const updated = await updateArticle(editingArticle.value.id, formData)
      logArticleAction('update', `æ›´æ–°æ–‡ç« ã€Œ${updated.title ?? editingArticle.value.title}ã€`, 'success', editingArticle.value.id)
    } else {
      const created = await createArticle(formData)
      logArticleAction('create', `åˆ›å»ºæ–‡ç« ã€Œ${created.title ?? formData.title}ã€`, 'success', created.id)
    }
    showEditModal.value = false
    fetchArticles()
  } catch (error) {
    alert('ä¿å­˜å¤±è´¥')
    const action: ArticleLogAction = editingArticle.value ? 'update' : 'create'
    logArticleAction(action, `ä¿å­˜æ–‡ç« ã€Œ${formData.title || 'æœªå‘½åæ–‡ç« '}ã€å¤±è´¥`, 'failure', editingArticle.value?.id)
  } finally {
    saving.value = false
  }
}

onMounted(() => {
  fetchArticles()
  const stopRefresh = onAdminRefresh((detail) => {
    if (detail.entity === 'all' || detail.entity === 'articles') {
      fetchArticles()
      logArticleAction('refresh', `å…¨å±€è§¦å‘ ${detail.action ?? 'refresh'} åŒæ­¥æ–‡ç« æ•°æ®`)
    }
  })
  cleanupFns.push(stopRefresh)
})

onUnmounted(() => {
  cleanupFns.forEach((stop) => stop())
})
</script>

<style scoped>
.admin-article-manager {
  padding: 1.5rem;
}
.manager-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
