        <BaseButton
          variant="danger"
          :disabled="logs.length === 0"
          @click="handleClear"
        >
          æ¸…ç©ºæ—¥å¿—
        </BaseButton>
      </div>
    </div>

    <div class="log-stats">
      <BaseCard class="stat-card">
        <div class="stat-label">
          æ—¥å¿—æ€»æ•°
        </div>
        <div class="stat-value">
          {{ totalLogs }}
        </div>
      </BaseCard>
      <BaseCard class="stat-card">
        <div class="stat-label">
          æœ€è¿‘ä¸€æ¡
        </div>
        <div class="stat-value">
          {{ lastLog ? formatDate(lastLog.timestamp) : '-' }}
        </div>
      </BaseCard>
      <BaseCard class="stat-card">
        <div class="stat-label">
          ç­›é€‰ç»“æœ
        </div>
        <div class="stat-value">
          {{ filteredLogs.length }}
        </div>
      </BaseCard>
    </div>

    <BaseCard>
      <div class="filters">
        <input
          v-model="filters.search"
          class="filter-input"
          placeholder="æœç´¢æè¿°æˆ–æ“ä½œäºº"
        >
        <select v-model="filters.entity">
          <option value="all">
            å…¨éƒ¨å®ä½“
          </option>
          <option
            v-for="entity in entityOptions"
            :key="entity.value"
            :value="entity.value"
          >
            {{ entity.label }}
          </option>
        </select>
        <select v-model="filters.action">
          <option value="all">
            å…¨éƒ¨åŠ¨ä½œ
          </option>
          <option
            v-for="action in actionOptions"
            :key="action.value"
            :value="action.value"
          >
            {{ action.label }}
          </option>
        </select>
        <select v-model="filters.result">
          <option value="all">
            å…¨éƒ¨ç»“æœ
          </option>
          <option value="success">
            æˆåŠŸ
          </option>
          <option value="failure">
            å¤±è´¥
          </option>
        </select>
      </div>

      <div
        v-if="loading"
        class="loading-state"
      >
        <LoadingSpinner />
        <p>åŠ è½½æ—¥å¿—ä¸­...</p>
      </div>

      <div
        v-else-if="pagedLogs.length === 0"
        class="empty-state"
      >
        <p>æš‚æ— ç¬¦åˆæ¡ä»¶çš„æ—¥å¿—è®°å½•</p>
      </div>

      <div
        v-else
        class="table-wrapper"
      >
        <table class="log-table">
          <thead>
            <tr>
              <th>æ—¶é—´</th>
              <th>å®ä½“</th>
              <th>æ“ä½œ</th>
              <th>æè¿°</th>
              <th>æ“ä½œäºº</th>
              <th>çŠ¶æ€</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="log in pagedLogs"
              :key="log.id"
            >
              <td>{{ formatDate(log.timestamp) }}</td>
              <td>
                <span class="pill">{{ entityLabel(log.entity) }}</span>
              </td>
              <td>{{ actionLabel(log.action) }}</td>
              <td>
                <div class="message">
                  {{ log.message }}
                </div>
                <small
                  v-if="log.metadata"
                  class="metadata"
                >
                  {{ stringifyMeta(log.metadata) }}
                </small>
              </td>
              <td>{{ log.operator || 'ç³»ç»Ÿ' }}</td>
              <td>
                <span
                  class="result-pill"
                  :class="log.result"
                >
                  {{ log.result === 'success' ? 'æˆåŠŸ' : 'å¤±è´¥' }}
                </span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div
        v-if="filteredLogs.length > pagination.pageSize"
        class="pagination"
      >
        <BasePagination
          :current="pagination.current"
          :page-size="pagination.pageSize"
          :total="filteredLogs.length"
          @change="handlePageChange"
