<template>
  <div class="admin-project-manager">
    <div class="manager-header">
      <h2>é¡¹ç›®ç®¡ç†</h2>
      <div class="header-actions">
        <BaseButton
          variant="primary"
          @click="handleCreate"
        >
          + æ–°å»ºé¡¹ç›®
        </BaseButton>
        <BaseButton
          variant="secondary"
          @click="handleRefresh"
        >
          åˆ·æ–°
        </BaseButton>
      </div>
    </div>

    <!-- é¡¹ç›®åˆ—è¡¨ -->
    <div class="projects-section">
      <BaseCard class="content-card">
        <div
          v-if="loading"
          class="loading-container"
        >
          <LoadingSpinner />
        </div>
        
        <div
          v-else-if="projects.length === 0"
          class="empty-state"
        >
          <p>æš‚æ— é¡¹ç›®æ•°æ®</p>
        </div>

        <div
          v-else
          class="projects-table-container"
        >
          <table class="data-table">
            <thead>
              <tr>
                <th>ID</th>
                <th>é¡¹ç›®åç§°</th>
                <th>åˆ†ç±»</th>
                <th>çŠ¶æ€</th>
                <th>è¿›åº¦</th>
                <th>æ“ä½œ</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="project in projects"
                :key="project.id"
              >
                <td>{{ project.id }}</td>
                <td>{{ project.name }}</td>
                <td>{{ project.category }}</td>
                <td>
                  <span :class="['status-badge', String(project.status ?? 'planning').toLowerCase()]">
                    {{ project.status || 'æœªè®¾ç½®' }}
                  </span>
                </td>
                <td>{{ project.progress }}%</td>
                <td class="actions-cell">
                  <button
                    class="action-btn edit"
                    @click="handleEdit(project)"
                  >
                    ç¼–è¾‘
                  </button>
                  <button
                    class="action-btn delete"
                    @click="handleDelete(project)"
                  >
                    åˆ é™¤
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        
        <!-- åˆ†é¡µ -->
        <div
          v-if="totalPages > 1"
          class="pagination-container"
        >
          <BasePagination 
            :current="currentPage" 
            :page-size="pageSize"
