    >
      <form
        class="edit-form"
        @submit.prevent="saveProject"
      >
        <div class="form-group">
          <label>é¡¹ç›®åç§°</label>
          <input
            v-model="formData.name"
            type="text"
            required
            class="form-input"
          >
        </div>
        <div class="form-group">
          <label>æè¿°</label>
          <textarea
            v-model="formData.description"
            class="form-textarea"
          />
        </div>
        <div class="form-group">
          <label>åˆ†ç±»</label>
          <select
            v-model="formData.category"
            class="form-select"
          >
            <option value="DEVELOPMENT">
              å¼€å‘
            </option>
            <option value="RESEARCH">
              ç ”ç©¶
            </option>
            <option value="COMPETITION">
              ç«èµ›
            </option>
          </select>
        </div>
        <div class="form-group">
          <label>çŠ¶æ€</label>
          <select
            v-model="formData.status"
            class="form-select"
          >
            <option value="PLANNING">
              è§„åˆ’ä¸­
