<script setup lang="ts">
import { ref, onMounted, onUnmounted, reactive } from 'vue'
import { getProjects, createProject, updateProject, deleteProject } from '@/api/project'
import { Category, Status, type Project } from '@/types/entities'
import BaseButton from '@/components/common/BaseButton.vue'
import BaseCard from '@/components/common/BaseCard.vue'
import BaseModal from '@/components/common/BaseModal.vue'
import BasePagination from '@/components/common/BasePagination.vue'
import LoadingSpinner from '@/components/common/LoadingSpinner.vue'
import { onAdminRefresh } from '@/utils/adminEvents'
import { recordAdminOperation } from '@/composables/useAdminLogs'

const projects = ref<Project[]>([])
const loading = ref(false)
const saving = ref(false)
const currentPage = ref(1)
const totalPages = ref(1)
const totalItems = ref(0)
const pageSize = ref(10)
const showEditModal = ref(false)
const editingProject = ref<Project | null>(null)
const cleanupFns: Array<() => void> = []

type ProjectLogAction = 'create' | 'update' | 'delete' | 'refresh'

const logProjectAction = (action: ProjectLogAction, message: string, result: 'success' | 'failure' = 'success', targetId?: string | number) => {
  recordAdminOperation({
    entity: 'projects',
    action,
    message,
    targetId,
    result
  }).catch((error) => {
    console.warn('è®°å½•é¡¹ç›®æ“ä½œæ—¥å¿—å¤±è´¥:', error)
  })
}

const formData = reactive<Partial<Project>>({
  name: '',
  description: '',
  category: Category.Development,
  status: Status.Planning,
  progress: 0
})

const fetchProjects = async () => {
  loading.value = true
  try {
  const res = await getProjects({ page: currentPage.value - 1, size: pageSize.value })
    // é€‚é… Mock æ•°æ®ç»“æ„ï¼ˆå¦‚æœæ˜¯æ•°ç»„ç›´æ¥è¿”å›ï¼Œå¦‚æœæ˜¯åˆ†é¡µå¯¹è±¡å– contentï¼‰
    if (Array.isArray(res)) {
      projects.value = res
      totalPages.value = 1
      totalItems.value = res.length
    } else {
      projects.value = res.content || []
      totalPages.value = res.totalPages || 1
      totalItems.value = res.totalElements || projects.value.length
    }
  } catch (error) {
    console.error('Failed to fetch projects:', error)
  } finally {
    loading.value = false
  }
}

const handleRefresh = () => {
  fetchProjects()
  logProjectAction('refresh', 'æ‰‹åŠ¨åˆ·æ–°é¡¹ç›®åˆ—è¡¨')
}

const handlePageChange = (page: number) => {
  currentPage.value = page
  fetchProjects()
}

const handleCreate = () => {
  editingProject.value = null
  Object.assign(formData, {
    name: '',
    description: '',
    category: Category.Development,
    status: Status.Planning,
    progress: 0
  })
  showEditModal.value = true
}

const handleEdit = (project: Project) => {
  editingProject.value = project
  Object.assign(formData, project)
  showEditModal.value = true
}

const handleDelete = async (project: Project) => {
  if (!confirm(`ç¡®å®šè¦åˆ é™¤é¡¹ç›® "${project.name}" å—ï¼Ÿ`)) return
  try {
    await deleteProject(project.id!)
    fetchProjects()
    logProjectAction('delete', `åˆ é™¤é¡¹ç›®ã€Œ${project.name}ã€`, 'success', project.id)
  } catch (error) {
    alert('åˆ é™¤å¤±è´¥')
    logProjectAction('delete', `åˆ é™¤é¡¹ç›®ã€Œ${project.name}ã€å¤±è´¥`, 'failure', project.id)
  }
}

const saveProject = async () => {
  saving.value = true
  try {
    if (editingProject.value) {
      const updated = await updateProject(editingProject.value.id!, formData)
      logProjectAction('update', `æ›´æ–°é¡¹ç›®ã€Œ${updated.name ?? editingProject.value.name}ã€`, 'success', editingProject.value.id)
    } else {
      const created = await createProject(formData)
      logProjectAction('create', `åˆ›å»ºé¡¹ç›®ã€Œ${created.name ?? formData.name}ã€`, 'success', created.id)
    }
    showEditModal.value = false
    fetchProjects()
  } catch (error) {
    alert('ä¿å­˜å¤±è´¥')
    const action: ProjectLogAction = editingProject.value ? 'update' : 'create'
    logProjectAction(action, `ä¿å­˜é¡¹ç›®ã€Œ${formData.name || 'æœªå‘½åé¡¹ç›®'}ã€å¤±è´¥`, 'failure', editingProject.value?.id)
  } finally {
    saving.value = false
  }
}

onMounted(() => {
  fetchProjects()
  const stopRefresh = onAdminRefresh((detail) => {
    if (detail.entity === 'all' || detail.entity === 'projects') {
      fetchProjects()
      logProjectAction('refresh', `å…¨å±€è§¦å‘ ${detail.action ?? 'refresh'} åŒæ­¥é¡¹ç›®æ•°æ®`)
    }
  })
  cleanupFns.push(stopRefresh)
})

onUnmounted(() => {
  cleanupFns.forEach((stop) => stop())
})
</script>

